<template>
    <div class="about-address-editor">
        <navigate :title="navigateTitle" position="absolute"/>
        <div class="wrapper">
            <div class="border-1px-b">
                <mu-text-field v-model="query" icon="search" placeholder="请输入地名搜索" full-width solo/>
            </div>
            <mu-load-more>
                <mu-list>
                    <mu-list-item class="border-1px-b shrink" v-for="(item, index) in list" :key="index"
                                  @click.native="handlerItem(item)">
                        <mu-list-item-title>{{item.name}}</mu-list-item-title>
                    </mu-list-item>
                </mu-list>
            </mu-load-more>
        </div>
    </div>
</template>

<script>
    import Navigate from "../../../base/navigate/navigate"
    import {getNavigationList} from "../../../api/navigational"
    import {debounce} from "../../../types/index"
    import {saveOrUpdateIAddress} from "../../../api/personal"

    export default {
        name: "about-address-editor",
        data() {
            return {
                /***
                 * 搜索关键词
                 */
                query: '',
                /***
                 * 地址列表
                 */
                list: []
            }
        },
        computed: {
            /***
             * 导航栏标题
             * @return {string}
             */
            navigateTitle() {
                return this.$route.query.id ? '修改常用地址' : '添加常用地址'
            }
        },
        created() {
            /***
             * 监听关键词变化提供搜索结果
             */
            this.$watch('query', debounce(value => {
                if (!value) return

                const data = {
                    typeId: '013008',
                    name: value,
                    pageNumber: 1,
                    pageSize: 100
                }

                getNavigationList(data).then(res => {
                    this.list = res
                })
            }, 300))
        },
        activated() {
            this.query = ''
        },
        methods: {
            handlerItem(item) {
                const data = {
                    name: item.name,
                    longitude: item.longitude,
                    latitude: item.latitude
                }

                saveOrUpdateIAddress(data).then(res => {
                    this.$toast('添加成功')
                    this.$router.back()
                    this.$emit('save')
                })
            }
        },
        components: {
            Navigate
        }
    }
</script>

<style lang="stylus" scoped>
    .about-address-editor
        padding-top 46px
        background-color #fff
        .wrapper
            width 100%
            height 100%
            overflow hidden
            .mu-item-title
                font-size 14px
                color #999
</style>



