import axios from '../common/js/axios'

/***
 * 助航信息
 * @param data
 * @param data.types          类型 ID
 * @param data.name           标题 为空查全部
 * @param data.pageNumber     页码
 * @param data.pageSize       每页显示条数
 * @returns {*}
 */
export function getNavigationList(params) {
    const url = `/search/channel`
    // const url = `https://www.gdhdswzx.xyz:10001/app/search/channel`

    return axios.get(url, {params})
}

/***
 * 助航资讯详情
 * @param params.typeId  // 类型
 * @param params.id    // id
 * @returns {*}
 */
export function getNavigationDetail(params) {
    const url = `/channel/navi/queryinfo`

    return axios.get(url, {params})
}








